import { Menu, Icon, Button, Switch } from "antd";
import * as React from "react";
import Sider from "antd/lib/layout/Sider";
import {
  Link,
  Route,
  BrowserRouter,
  Router,
  HashRouter
} from "react-router-dom";
import HackathonExperience from "./HackathonExperienceComponent";
import { NavHashLink, HashLink } from "react-router-hash-link";
import AnchorLink from "antd/lib/anchor/AnchorLink";
import VolunteerTimeLine from "./VolunteerTimeLine";
import AboutMeComponent from "./AboutMeComponent";
import ReactDOM from "react-dom";
import PortfolioComponent from "./PortfolioComponent";
const SubMenu = Menu.SubMenu;

export default class Sidebar extends React.Component {
  state = {
    collapsed: true
  };

  onCollapse = (collapsed: boolean) => {
    this.setState({ collapsed });
  };

  render() {
    return (
      <Sider
        collapsible
        collapsed={this.state.collapsed}
        onCollapse={this.onCollapse}
      >
        <div className="logo" />

        <HashRouter basename={process.env.PUBLIC_URL}>
          <Menu theme="dark" defaultSelectedKeys={["1"]} mode="inline">
            <Menu.Item key="1">
              <HashLink to="/#aboutMe">
                <Icon type="user" />
                <span>About Me</span>
              </HashLink>
            </Menu.Item>
            <Menu.Item key="2">
              <HashLink to="/#workExperience">
                <Icon type="team" />
                <span>Work Experience</span>
              </HashLink>
            </Menu.Item>
            <Menu.Item key="3">
              <HashLink to="/#portfolio">
                <Icon type="branches" />
                <span>Portfolio</span>
              </HashLink>
            </Menu.Item>
            <Menu.Item key="4">
              <HashLink to="/#volunteerExperience">
                <Icon type="appstore" />
                <span>Volunteer Experience</span>
              </HashLink>
            </Menu.Item>
            <Menu.Item key="5">
              <HashLink to="/#hackathonExperience">
                <Icon type="experiment" />
                <span>Hackathon Experience</span>
              </HashLink>
            </Menu.Item>
          </Menu>
        </HashRouter>
      </Sider>
    );
  }
}
